body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#root{
  height:100vh;
}

.create-question-container{

  display: inline-block;
  height:100%;
  width:100%;
  border-radius: 10px;
  box-shadow: 0 1px 15px rgba(0,0,0,0.12), 0 1px 16px rgba(0,0,0,0.24);
  background: white;
}
.create-question-item-container{
  display: inline-block;
  height:115px;
  width:100%;
  border-radius: 5px;
  box-shadow: 0 1px 5px rgba(0,0,0,0.12), 0 1px 6px rgba(0,0,0,0.24);
  background: white;
  margin-bottom:10px;
  padding:10px 20px 10px 50px;
}
.height-full{
  height:100%;
}
.padding-30{
  padding:35px;
}
.add-question-button{
  width:45px;

}
.create-question-input{
  border-width:0px 0px 0px 2px!important;
  border-radius: 0px !important;
  border-color: #13C64F !important;
  border-right-color: white !important;
}
.create-question-input:active,
.create-question-input:focus,
.create-question-input:hover{
  border-width:0px 0px 0px 2px!important;
  border-radius: 0px !important;
  border-color: #13C64F !important;
  border-right-width: 0px !important !important;
  border-right-color: white !important;
  box-shadow: 0 0 0 0px rgba(29, 165, 122, 0.2) !important;
}


.preview-header{
  height:7%;
  border-top-right-radius:5px;
  border-top-left-radius:5px;

}
.preview-body{
  height:93%;
  border-bottom-right-radius:5px;
  border-bottom-left-radius:5px;
  background: #13C64F;
}

